@import '../../assets/css/var.scss';

.wallet {
  &-wrap-modal-title {
    padding: 16px 20px 12px;
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
    line-height: 22px;
  }
  &-wrap {
    display: flex;
    align-items: center;
    padding: 10px 30px 30px 20px;
  }

  &-wrap-l {
    flex: 1;
    margin-right: 10px;
  }

  &-wrap-info {
    display: flex;
    align-items: center;
  }

  &-wrap-mobile {
    display: flex;
    align-items: center;
    margin-bottom: 17px;
    &-address-wrap {
      display: flex;
      align-items: center;
    }
  }

  &-logo {
    width: 40px;
    height: 40px;
    margin-right: 12px;
  }

  &-info {
    font-size: 16px;
    line-height: 22px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.5);
  }

  &-btn {
    background: #5a14e1;
    border-radius: 15px;
    font-size: 12px;
    font-weight: 600;
    color: #ffffff;
    line-height: 17px;
    padding: 6px 28px;
    cursor: pointer;
  }

  &-address {
    margin-top: 5px;
    font-size: 12px;
    font-weight: 400;
    color: #ffffff;
    line-height: 17px;
  }

  &-mobile-address {
    font-size: 16px;
    font-weight: 400;
    color: #ffffff;
    line-height: 17px;
  }

  &-label {
    font-size: 12px;
    font-weight: 400;
    color: #ffffff;
    opacity: 0.5;
  }

  &-copy {
    display: inline-block;
    width: 12px;
    height: 14px;
    cursor: pointer;
    background: url('../../assets/images/menus/file_copy.svg') no-repeat center / contain;
    vertical-align: middle;
    margin-left: 8px;
  }
}

.wallet-address-modal {
  .ant-modal-body {
    background-color: rgba(54, 45, 74, 1);
  }
  .extension-from {
    display: flex;
    justify-content: flex-start;
    padding: 8px 30px;
    margin-bottom: 14px;
    .inline-form {
      display: flex;
      align-items: center;
    }
    .ant-form-item {
      margin-bottom: 0 !important;
      margin-right: 6px;
    }
    .ant-form-item-label {
      margin: 0;
    }
    .ant-form-item-label > label {
      font-size: 12px;
      color: #ffffff;
    }
    .ant-input-number {
      width: 55px;
      background: #444454;
      color: #ffffff;
      border-radius: 5px;
      border: 1px solid rgba(255, 255, 255, 0.11);
    }
    .ant-form-item-has-error .ant-input-number:not([disabled]):hover {
      background-color: #444454;
    }
    .ant-input-number-handler-wrap {
      background-color: #444454;
      border-left: 1px solid rgba(255, 255, 255, 0.11);
      border-radius: 0 4px 4px 0;
      > span {
        border-color: rgba(255, 255, 255, 0.11);
      }
    }
    // .ant-input-number-handler-up:hover {
    //   height: 48% !important;
    // }
    // .ant-input-number-handler-down:hover {
    //   height: 50% !important;
    // }
    // .ant-input-number-handler-wrap:hover .ant-input-number-handler {
    //   height: 48%;
    // }
    .ant-input-number-handler-up-inner,
    .ant-input-number-handler-down-inner {
      color: rgba(255, 255, 255, 0.5);
    }
    .ant-input-number-handler:hover .ant-input-number-handler-up-inner,
    .ant-input-number-handler:hover .ant-input-number-handler-down-inner {
      color: rgba(255, 255, 255, 1);
    }
    .ant-input-number-status-error:not(.ant-input-number-disabled):not(.ant-input-number-borderless).ant-input-number,
    .ant-input-number-status-error:not(.ant-input-number-disabled):not(
        .ant-input-number-borderless
      ).ant-input-number:hover {
      background-color: #444454;
    }

    .ant-input-number:hover:not(.ant-input-number-borderless) .ant-input-number-handler-down,
    .ant-input-number-focused:not(.ant-input-number-borderless) .ant-input-number-handler-down {
      border-color: rgba(255, 255, 255, 0.11);
    }
    .ant-input-number-handler:active {
      background: transparent;
    }

    .second-text {
      font-size: 12px;
      color: #ffffff;
      opacity: 0.5;
    }

    .wallet-default-btn {
      height: 25px;
      color: rgba(193, 160, 255, 1);
      background: transparent;
      border-radius: 15px;
      border-color: rgba(193, 160, 255, 1);
      font-size: 12px;
      padding: 0px 10px;
      margin-left: 20px;
    }
  }
  .extension-from.extension-mobile-from {
    justify-content: space-between;
    background: #3a3a4b;
    border-radius: 4px;
    padding: 12px 4px 12px 10px;
    .mb-10 {
      margin-bottom: 10px;
    }
    .flex-form {
      display: flex;
      align-items: center;
    }
    .ant-form-item {
      display: flex;
      align-items: end;
    }
    .ant-form-item-control-input {
      min-height: 21px;
    }
    .ant-form-item-label {
      opacity: 0.5;
      margin-right: 6px;
    }
    .ant-input-number {
      height: 21px;
      font-size: 12px;
      line-height: 18px;
      .ant-input-number-input {
        height: 21px;
      }
    }
  }
}

.dark-modal.wallet-modal.ant-modal {
  border-radius: 20px;
  .ant-modal-close {
    .anticon-close {
      background: url(../../assets/images/close_white.svg) 0 0 no-repeat;
      width: 35px;
      height: 35px;
      background-size: 100% 100%;
    }
  }
  .ant-modal-content {
    background: #2f2f41;
  }
  .ant-modal-header {
    background: #2f2f41;
    color: #fff;
    padding-top: 30px;
    padding-bottom: 40px;
    .ant-modal-title {
      color: #fff;
    }
  }
  .ant-modal-body {
    background: #2f2f41;
    color: #fff;
    padding: 0 50px 25px !important;
  }
  .wallet-item {
    display: flex;
    align-items: center;
    border-radius: 10px;
    cursor: pointer;
    padding: 20px 30px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    font-size: 17px;
    margin-bottom: 20px;
    border: 2px solid transparent;
    &:hover {
      background-color: rgba(255, 255, 255, 0.1);
      border-color: rgba(255, 255, 255, 0.1);
    }
    img {
      width: 40px;
      height: 40px;
    }
    .wallet-item-logo {
      flex: 1;
      text-align: left;
    }
    .wallet-item-name {
      // flex: 1;
      // text-align: right;
      // margin-left: 10px;
    }
    .wallet-item-usetip {
      padding: 12px;
      margin-left: 12px;
    }
  }
  .tronlink-tips {
    margin-top: 20px;
    .tronlink-tips-item {
      margin-bottom: 8px;
      text-align: center;
      &:last-child {
        margin-bottom: 0;
      }
    }
    .tronlink-tips-text {
      font-size: 15px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #ffffff;
      line-height: 21px;
    }
    .tronlink-tips-link {
      margin-right: 10px;
      color: #c1a0ff;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .address-con {
    display: flex;
    .address-logo {
      margin-right: 12px;
      width: 40px;
      height: 40px;
    }

    .tip-text {
      text-align: left;
      font-size: 14px;
      line-height: 16px;
      color: #7c8ead;
    }

    .address-tex {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
    }

    .copy {
      color: #5916e1;
    }
  }

  .ant-modal-close {
    right: 12px;
    top: 5px;
  }
  .ant-modal-close-x {
    font-size: 37px;
  }
}

.dark-modal.ant-modal-wrap,
.dark-modal.ant-modal,
.dark-modal.ant-modal-wrap.token-select-modal {
  color: #fff;
  padding: 0;
  .ant-modal-header {
    background: #2f2f41;
    color: #fff;
    border: none;
    border-radius: 20px 20px 0 0;
    padding: 20px 15px;
  }
  .ant-modal-content {
    background: #2f2f41;
    box-shadow: 0px 0px 12px 0px rgba(16, 16, 48, 0.3);
    border-radius: 20px 20px;
    padding: 0;
  }
  .ant-modal-close-x {
    color: #fff;
  }
  .ant-modal-body,
  .ant-modal-title {
    color: #fff;
  }
  .ant-modal-footer {
    border: none;
  }
  .ant-modal-body {
    padding: 0 !important;
    border-radius: 20px 20px;
    overflow: hidden;
  }
  .sun-stake-logo {
    vertical-align: middle;
  }
}

.dark-tabs {
  &-header {
    display: flex;
  }
  .ant-tabs-tabpane-active {
    background: rgba(77, 67, 102, 1);
  }

  .dark-tabs-item,
  .dark-tabs-item-active {
    flex: 1;
    height: 38px;
    line-height: 38px;
    text-align: center;
    background: rgba(64, 56, 88, 1);
    border: none;
    border-radius: 10px 10px 0 0;
    color: rgba(255, 255, 255, 0.5);
    font-size: 15px;
    font-weight: 400;
    cursor: pointer;
  }

  .dark-tabs-item-active {
    background: rgba(77, 67, 102, 1);
    color: #fff;
    position: relative;
  }
}

.dark-table.ant-table-wrapper {
  .ant-table {
    background: transparent;
  }
  .ant-empty {
    opacity: 0;
    display: hidden;
  }

  .ant-table-tbody > tr > td {
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  }

  .ant-table-tbody > tr.ant-table-row:hover > td {
    background: rgba(255, 255, 255, 0.05);
  }
  .ant-table-tbody > tr.ant-table-placeholder:hover > td {
    background: transparent;
  }
  .ant-table-tbody > tr.ant-table-placeholder {
    background: transparent;
    color: rgba(255, 255, 255, 0.05);
  }
  .ant-spin-blur::after {
    //background: rgba(255, 255, 255, 0.1);
    opacity: 0;
  }
  .ant-spin-nested-loading > div > .ant-spin {
    background: transparent;
  }
  .ant-spin-dot-item {
    background-color: #fff;
  }
  &-loading {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
  }
  .ant-table-footer {
    background: transparent;
  }
  .ant-spin-nested-loading > div > .ant-spin .ant-spin-text {
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    text-shadow: none;
    font-weight: 400;
  }
}

.trade-status-tag {
  background: #78ffc7;
  border-radius: 11px;
  font-size: 12px;
  font-weight: 600;
  line-height: 17px;
  padding: 2px 10px;
  display: inline-block;
  &-bar {
    border-radius: 0px 100px 100px 0px;
  }
}

.trade-cell-light {
  font-size: 15px;
  font-weight: 600;
  color: #ffffff;
  line-height: 21px;
}

.trade-cell-gray {
  font-size: 12px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.5);
  line-height: 17px;
}

.trade-pool-info {
  display: inline-block;
  vertical-align: top;
  margin: 0 10px;
}

.trade-text-right {
  text-align: right;
}

.dark-pagination.ant-table-wrapper {
  .ant-pagination {
    .ant-pagination-item,
    .ant-pagination-jump-next,
    .ant-pagination-jump-prev {
      border-radius: 2px;
      margin-right: 8px;
    }
    li:nth-last-child(2) {
      margin-right: 0;
    }

    .ant-pagination-item:not(.ant-pagination-item-active),
    .ant-pagination-jump-next,
    .ant-pagination-jump-prev {
      background: transparent;
      border: 1px solid rgba(255, 255, 255, 0.5);
      a,
      .ant-pagination-item-ellipsis {
        color: rgba(255, 255, 255, 0.5);
      }
    }

    .ant-pagination-item-active {
      background: #6d00f4;
      border: 1px solid rgba(230, 217, 255, 0.3);
      a {
        color: #fff;
      }
    }

    .ant-pagination-jump-next .ant-pagination-item-link-icon,
    .ant-pagination-jump-prev:hover .ant-pagination-item-link-icon {
      color: rgba(255, 255, 255, 0.5);
    }
    .ant-pagination-jump-prev .ant-pagination-item-container .ant-pagination-item-link-icon,
    .ant-pagination-jump-next .ant-pagination-item-container .ant-pagination-item-link-icon {
      color: rgba(255, 255, 255, 0.5);
    }
  }
  .ant-pagination-disabled {
    opacity: 0.25;
  }
}

.trade-liquidity-token-wrap {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.trade-liquidity-token-from {
  width: 40%;
  line-height: 21px;
  margin-top: 8px;
  display: flex;
  align-items: center;
}
.trade-liquidity-token-to {
  display: flex;
  align-items: center;
}
.trade-list-vertical-bottom {
  vertical-align: bottom;
}
.trade-liquidity-token-name {
  margin: 0 12px 0 6px;
  font-size: 15px;
}

.mobile-account-center-modal.ant-drawer {
  .ant-drawer-content {
    background-color: rgba(47, 47, 65, 1);
  }
  .ant-drawer-body {
    padding: 20px 15px;
  }

  .wallet-wrap {
    padding: 0;
    margin-bottom: 22px;
    .wallet-address {
      font-size: 16px;
      line-height: 22px;
      font-weight: 400;
    }
  }

  .extension-from {
    padding: 0;
  }
}
.mobile-dark-tabs {
  &-header {
    display: flex;
  }

  &-item,
  &-item-active {
    font-size: 14px;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.5);
    line-height: 20px;
    margin-right: 20px;
    padding-bottom: 20px;
    cursor: pointer;
  }
  &-item:last-child {
    margin-right: 0;
  }
  &-item-active {
    color: #fff;
    position: relative;

    &:after {
      position: absolute;
      bottom: 14px;
      transform: translateX(-50%);
      left: 50%;
      content: '';
      width: 20px;
      height: 4px;
      background: linear-gradient(135deg, #9017ff 0%, #5a14e1 100%);
      border-radius: 2px;
    }
  }
}

.mobile-trade-item {
  background: #444454;
  border-radius: 5px;
  margin-bottom: 10px;
  user-select: none;
  &-header {
    height: 33px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    &-value {
      flex: 1;
      margin: 0 10px;
      font-size: 12px;
      font-weight: 600;
      color: #ffffff;
      line-height: 17px;
    }
    &-date {
      font-size: 12px;
      font-weight: 400;
      color: rgba(255, 255, 255, 0.5);
      line-height: 17px;
      margin-right: 10px;
    }
  }
  &-content {
    padding: 10px;
  }
  &-liquidity {
    .mobile-trade-item-content {
      padding: 10px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    .trade-token-item {
      width: calc((100% - 26px) / 2);
      flex-shrink: 0;
      margin-top: 20px;
      &:nth-child(1) {
        margin-top: 0;
      }
      &:nth-child(2) {
        margin-top: 0;
      }
    }
  }
  .sun-stake-logo {
    margin-right: 10px;
  }
}
.trade-font {
  &-14 {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
  }
  &-w-600 {
    font-weight: 600;
  }
  &-12 {
    font-size: 12px;
    line-height: 17px;
    font-weight: 400;
  }
  &-light {
    color: #fff;
  }
  &-gray {
    color: rgba(255, 255, 255, 0.5);
  }
}
.trade-token-item {
  display: flex;
  width: 100%;
  .sun-icon-img {
    margin-right: 10px;
  }
  &-amount {
    margin-bottom: 3px;
    display: flex;
    justify-content: space-between;
  }
  &-right {
    flex: 1;
  }
}

@keyframes alwaysLoading {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(270deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.infinite-loading {
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
  &-circle {
    animation: alwaysLoading 700ms ease infinite;
    overflow: hidden;
  }
}
.account-list-empty.sun-empty-wrap {
  margin-bottom: 0;
  background: transparent;
}
.account-list-tip {
  font-size: 12px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.3);
  line-height: 17px;
  text-align: center;
}
.trade-liquidity-pc-list-time {
  vertical-align: top;
  .trade-cell-gray {
    line-height: 1em;
  }
}

// 消息中心样式
.account-notice-center {
  color: rgba(255, 255, 255, 0.7);
  .account-notice-list {
    max-height: 400px;
    overflow-y: auto;
    .account-notice-item {
      padding: 18px 20px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.01);
      cursor: pointer;
      &:hover {
        background: rgba(255, 255, 255, 0.05);
      }

      &:last-child {
        border: 0;
      }
      .account-notice-title {
        display: flex;
        align-items: center;
        margin-bottom: 8px;

        .account-notice-title-info {
          display: flex;
          flex: 1;
          align-items: center;
          .account-notice-title-name {
            display: flex;
            align-items: center;
            margin-right: 10px;
            font-size: 16px;
            font-weight: 600;
            color: rgba(255, 255, 255, 0.4);
            line-height: 22px;
          }
          .account-notice-icon-new {
            display: block;
            width: 32px;
          }
        }
        .account-notice-time {
          font-size: 12px;
          font-weight: 400;
          line-height: 17px;
          color: rgba(255, 255, 255, 0.3);
        }
      }

      .account-list-content {
        font-size: 14px;
        font-weight: 200;
        line-height: 22px;
        color: rgba(255, 255, 255, 0.4);
      }
    }

    .account-notice-item-new {
      .account-notice-title {
        .account-notice-title-info {
          .account-notice-title-name {
            color: rgba(255, 255, 255, 1);
          }
        }
        .account-notice-time {
          color: rgba(255, 255, 255, 0.7);
        }
      }
      .account-list-content {
        color: rgba(255, 255, 255, 1);
      }
    }
  }

  .account-list-empty {
  }
}

@include mobile-only {
  .dark-modal.wallet-modal.ant-modal {
    .ant-modal-header {
      padding-top: 20px;
      padding-bottom: 20px;
    }

    .ant-modal-body {
      padding: 0 20px 20px !important;
    }

    .wallet-item {
      padding: 10px 20px;
      font-size: 14px;
      img {
        width: 30px;
        height: 30px;
      }
    }

    .tronlink-tips {
      .tronlink-tips-item {
        font-size: 12px;
        margin-bottom: 6px;
        a {
          font-size: 12px;
        }
      }
      .tronlink-tips-text {
        font-size: 12px;
      }
    }

    .ant-modal-close-x {
      width: 30px;
      height: 30px;
      line-height: 30px;
      font-size: 22px;
    }
    .ant-modal-close .anticon-close {
      width: 22px;
      height: 22px;
    }
  }

  .account-notice-center {
    .account-notice-list {
      .account-notice-item {
        padding: 10px 0;
        .account-notice-title {
          .account-notice-title-info {
            .account-notice-title-name {
              margin-right: 5px;
              font-size: 14px;
              line-height: 20px;
            }
          }
          .account-notice-title-r {
            margin-left: 5px;
          }
          .account-notice-icon-new {
            width: 28px;
          }
        }

        .account-list-content {
          font-size: 13px;
          line-height: 20px;
        }
      }
    }
  }
}
