$close: #cccccc;
$in: #78ffc7;
$out: #ff8e15;

.v3-text-range {
  font-size: 12px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  cursor: pointer;
  > span {
    display: inline-block;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    margin-right: 4px;
  }
  &.close {
    color: $close;
    > span {
      background: $close;
    }
  }
  &.in {
    color: $in;
    > span {
      background: $in;
    }
  }
  &.out {
    color: $out;
    > span {
      background: $out;
    }
  }
}

.v3-range-badge {
  border-radius: 0px 10px 0px 10px;
  position: absolute;
  right: 0;
  top: 0;
  padding: 4px 15px;
  color: #ffffff;
  cursor: pointer;
  &.close {
    background: $close;
  }
  &.in {
    background: #00ca76;
  }
  &.out {
    background: $out;
  }
}
