.sun-empty {
  &-wrap {
    width: 100%;
    min-height: 213px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #2f2f41;
    border-radius: 10px;
    margin-bottom: 30px;
  }
  &-img {
    width: 180px;
    height: 120px;
    margin-bottom: 8px;
    opacity: 0.5;
  }
  &-text {
    font-size: 12px;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.5);
    line-height: 17px;
  }
  &-btn {
    padding: 0 22px;
    height: 29px;
    line-height: 29px;
    text-align: center;
    background: #8c51ff;
    border-radius: 15px;
    font-size: 12px;
    font-weight: 600;
    color: #ffffff;
    margin-top: 16px;
    cursor: pointer;
  }
}

@media screen and (max-width: 768px) {
  .sun-empty-wrap {
    .sun-empty-text.sun-empty-btn {
      font-size: 14px;
    }
    img {
      height: 60px;
    }
  }
}
