.sub-nav {
  &-wrap {
    position: relative;
    padding-left: 15px;
    box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.1);
    overflow-x: auto;
    &-div {
      width: max-content;
    }
  }
  &-item {
    margin-right: 18px;
    height: 36px;
    font-size: 14px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.5);
    line-height: 36px;
    position: relative;
    display: inline-block;
    &:link,
    &:visited {
      color: rgba(255, 255, 255, 0.5);
    }
    &:hover {
      color: #fff;
    }
    &-active {
      color: #fff;
      &:link,
      &:visited {
        color: #fff;
      }
      &:after {
        content: '';
        width: 23px;
        height: 4px;
        background: linear-gradient(135deg, #9017ff 0%, #5a14e1 100%);
        border-radius: 2px;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        position: absolute;
      }
    }
  }
  &-item-icon {
    width: 13px;
    margin-right: 4px;
  }
  &-item-corner {
    width: 20px;
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(100%, 0);
  }
  &-back {
    padding: 0 15px;
    height: 46px;
    width: 100%;
    display: flex;
    align-items: center;
    &-icon {
      width: 12px;
      height: 12px;
      cursor: pointer;
    }
  }

  &-box {
    position: relative;
    width: 100%;
    overflow: hidden;
  }

  &-right-wrap {
    position: absolute;
    right: 0;
    top: 0;
    width: 72px;
    height: 100%;
    background: linear-gradient(90deg, rgba(20, 21, 37, 0) 0%, #141525 50%, #141525 100%);
  }
  &-right-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 0;
    top: 0;
    width: 36px;
    height: 36px;
    background: rgba(20, 21, 37, 1);
    cursor: pointer;
    img {
      display: block;
      width: 10px;
      height: 10px;
    }
  }
}

.show-jump-right-nav {
  .sub-nav-wrap-div {
    padding-right: 36px;
  }
}

.pc-sub-nav {
  padding-left: 185px;
  box-shadow: none;
  background: rgba(255, 255, 255, 0.05);
  .sub-nav-item {
    margin-right: 40px;
    padding: 0 10px;
    height: 65px;
    line-height: 65px;
    font-size: 20px;
    &:last-child {
      margin-right: 0;
    }
  }
}
