.sun-stake-logo {
  display: inline-block;
  height: 40px;
  position: relative;
  img {
    height: 40px;
    vertical-align: center;
  }
  &-2 {
    width: 74px;
    img {
      position: absolute;
      &:first-child {
        left: 0;
        top: 0;
        z-index: 1;
      }
      &:last-child {
        right: 0;
        top: 0;
      }
    }
  }

  &-3 {
    width: 74px;
    img {
      position: absolute;
      height: 30px;
      &:first-child {
        left: 0;
        top: 7px;
      }
      &:nth-child(2) {
        left: 50%;
        transform: translateX(-50%);
        top: 0;
      }
      &:last-child {
        right: 0;
        top: 7px;
      }
    }
  }
}