@import './var.scss';
@import './mixins';

#root .top-menu-bar {
  display: flex;
  justify-content: space-between;
  padding: 18px 0;
  position: relative;
  margin: 0 auto;
  @include common-content-width;

  .menu-has-icon {
    position: relative;
  }
  .menu-right-corner-icon {
    position: absolute;
    right: -10px;
    top: -20px;
    z-index: 1;
    width: 32px;
    height: 20px;
  }
}

#root .top-menu-bar .inner-box {
  font-weight: 500;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  //height: 34px;
  flex-shrink: 0;

  //& > * {
  //  &:not(:last-child) {
  //    margin-right: 10px;
  //  }
  //}

  button {
    height: 70px;
    font-size: 16px;
    background: transparent;
    margin: 0;
    padding: 0;
    min-width: 0px !important;

    &:hover {
      a {
        color: #fff !important;
      }
    }
  }

  .sun-logo {
    height: 32px;
    margin-right: 50px;
  }

  .header-list-item a {
    position: relative;
    margin: 0;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.5);
    padding: 0 15px;
    display: inline-flex;
    align-items: center;
    font-size: 16px;
    line-height: 22px;
    height: 22px;

    &.is-hover-purple.active {
      color: #fff !important;
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        margin-left: -17px;
        bottom: -10px;
        width: 34px;
        height: 4px;
        background: #8c51ff;
        border-radius: 2px;
      }
    }

    &.disabled {
      color: #675d72 !important;
      cursor: not-allowed;
    }

    &.menu-title {
      pointer-events: none;
      margin-left: 0;
      // padding: 5px;
      // background: red;

      &:hover {
        background: #5915e1;
        color: #fff !important;
      }
    }

    &.new,
    &.hot {
      margin-right: -18px;
    }

    &.is-hover-purple:hover {
      color: #fff !important;
    }
  }

  .top-list a {
    color: #333;
    height: 20px;

    &.active {
      color: #5915e1;
    }
  }

  .top-list .selected a,
  .full-list .selected a {
    color: #fff;
  }

  .full-list {
    a {
      color: #333;
      font-weight: normal;
    }
  }

  .address-text {
    color: #5915e1;
    font-weight: normal;
  }
}

#root .top-menu-bar .account-box {
  flex-shrink: 0;
}

.full-list .selected a {
  background: #5915e1;
  color: #fff !important;
}

.connect-wallet {
  position: relative;
  text-align: center;
  color: #fff;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-sizing: border-box;
  border-radius: 20px;
  font-size: 14px;
  cursor: pointer;
  height: 36px !important;
  padding: 0 8px;
  white-space: nowrap;
  display: flex;
  align-items: center;
  .account-wallet-logo {
    border-radius: 50%;
    margin-right: 7px;
    overflow: hidden;
    width: 26px;
    height: 26px;
  }

  &:hover {
    background: #5915e1;
    border: 1px solid #5915e1;
  }

  .wallet-notice-tag {
    position: absolute;
    right: 0;
    top: -8px;
    transform: translateX(50%);
    padding: 0 5px;
    min-width: 16px;
    height: 16px;
    line-height: 16px;
    font-size: 12px;
    text-align: center;
    color: #fff;
    background: rgba(255, 83, 0, 1);
    border-radius: 16px;
    cursor: pointer;
  }
}

#root {
  position: relative;
  min-height: calc(100vh);
}

footer {
  width: 100%;
  // position: absolute;
  // bottom: 0;
}

.banner {
  width: 750px;
  // height: 172px;
  // margin: 20px auto 57px;
  margin: 20px auto 40px;
  text-align: center;
  overflow: hidden;
  // background: url(../images/banner.png) 0 0 no-repeat;
  // background-size: 100% 100%;

  .banner-title {
    margin-top: 48px;
    font-size: 36px;
    line-height: 30px;
    letter-spacing: 0.833333px;
    color: #ffffff;
    font-weight: 500;

    &.sp {
      font-size: 24px;
      line-height: 34px;
    }
  }

  .banner-des {
    margin-top: 16px;
    font-weight: normal;
    font-size: 16px;
    line-height: 32px;
    letter-spacing: 0.833333px;
    color: #ffffff;
  }

  .banner-tvl {
    color: #fff;
    margin: 30px 0 20px;

    .tvl-value {
      font-weight: bold;
      font-size: 50px;
      line-height: 68px;
      text-align: center;
      letter-spacing: 2px;
    }

    .tvl-title {
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      color: #999;
    }
  }

  .banner-tvl-detail {
    display: flex;
    justify-content: space-around;
    margin: 15px 0 20px;

    .tvl-value {
      font-weight: bold;
      font-size: 20px;
      line-height: 27px;
      text-align: center;
      letter-spacing: 2px;
      color: #fff;
    }

    .tvl-title {
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      color: #999;
    }
  }

  .banner-audit {
    display: inline-block;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    font-weight: normal;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: -0.4px;
    color: #ffffff;
    padding: 4px 10px;
    margin: 16px auto 0;

    a {
      color: #ded0f9;
      text-decoration: underline;
    }
  }

  &.pool-config {
    width: 100%;
    max-width: 1200px;

    .banner-title,
    .banner-des {
      text-align: left;
    }

    .banner-title {
      display: flex;
      align-items: center;
      font-weight: 600;
      font-size: 40px;
      line-height: 64px;
      color: #ffffff;

      > img {
        width: 36px;
        height: 36px;
        margin-left: 20px;
      }
    }

    .banner-des {
      font-size: 14px;
      line-height: 20px;
      color: rgba(255, 255, 255, 0.7);
      margin-top: 8px;

      a {
        color: #fff;
        text-decoration: underline;

        &:hover {
          color: #ded0f9;
        }
      }
    }
  }
}

