.common-modal {
  padding: 10px;

  .anticon-left,
  .anticon-close {
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url(../images/close.svg) 0 0 no-repeat;
    background-size: 100%;

    > svg {
      display: none;
    }
  }

  .anticon-left {
    width: 18px;
    height: 11px;
    background-image: url(../images/back@2x.png);
    cursor: pointer;
  }

  .trans-modal-cancel {
    margin-top: 30px;
  }

  .ant-modal-header,
  .ant-modal-body {
    background: #fff;
  }

  .ant-modal-header {
    border: none;
  }

  .ant-modal-body {
    padding: 20px 0;
  }

  .ant-modal-content {
    box-shadow: none;
  }

  .common-modal-title {
    text-align: center;
  }

  .step2-wallet {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 50px;
    font-size: 16px;

    .step2-wallet-logo {
      width: 40px;
      height: 40px;
      margin-right: 20px;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }

  .loading-img {
    display: block;
    margin-top: 55px;
    font-size: 90px;
    text-align: center;
    > svg {
      background: url(../images/wallet/loading.png);
      background-size: 90px 90px;
      width: 90px;
      height: 90px;
      > path {
        display: none;
      }
    }
  }

  .login-tip-text {
    text-align: center;
    margin-top: 65px;
    margin-bottom: 15px;
    a {
      color: #c1a0ff;
    }
  }

  .init-text {
    font-size: 17px;
    margin-top: 25px;
    text-align: center;
    font-weight: 600;
  }

  .ant-modal-close {
    top: 0;
    right: 0;
  }
}

.account-modal {
  .ant-modal-title {
    text-align: center;
  }

  .account-info {
    font-size: 16px;
    font-weight: 600;
    color: #fff;
