.mobile-header {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  height: 46px;
  display: flex;
  align-items: center;
  // max-width: 780px;

  &.hide {
    display: none !important;
  }
}

.mobile-logo {
  width: 77px;
  height: 30px;
  background: url(../images/SUNLogo.svg) 0 center no-repeat;
  background-size: 100%;
}

.mobile-category {
  width: 44px;
  height: 24px;
  background: url(../images/menu-btn.svg) 0 center no-repeat;
  background-size: 100%;
  cursor: pointer;
}

.mobile-menu-drawer {
  z-index: 2;

  .ant-drawer-content-wrapper {
    width: min(360px, 70%) !important;
  }

  .ant-drawer-body {
    a {
      display: block;
      line-height: 30px;
      margin-top: 12px;
      font-size: 16px;
      text-indent: 20px;
      border-radius: 5px;
      color: #333;
      padding-top: 8px;
      padding-bottom: 8px;

      &.active {
        &:not(.mobile-logo) {
          color: #fff !important;
          background: #5915e1;
        }
      }
    }

    .ant-collapse {
      font-size: 16px;
      line-height: 30px;
    }

    .ant-collapse-header {
      display: block;
      line-height: 30px;
      margin-top: 12px;
      font-size: 16px;
      text-indent: 0;
      // color: #333;
      border-radius: 4px !important;
      padding-left: 20px !important;

      .ant-collapse-arrow {
        top: 9px;
      }
    }

    .ant-collapse-content-box {
      padding-top: 0 !important;
      padding-bottom: 0 !important;

      span {
        cursor: pointer;
        color: #333;

        a {
          line-height: 19px;
          padding-top: 12px;
          padding-bottom: 12px;
          margin-top: 0;

          &.active {
            color: #5915e1 !important;
            background: transparent;
          }
        }
      }

      span.active {
        color: #5915e1;
      }
    }

    .ant-collapse-item {
      &.active {
        .ant-collapse-header {
          color: #fff !important;
          background: #5915e1;
        }
      }
    }

    // .ant-collapse:not(.mobile-language-select) {
    //   .ant-collapse-item {
    //     &.ant-collapse-item-active {
    //       .ant-collapse-header {
    //         color: #fff !important;
    //         background: #5915e1;
    //       }
    //     }
    //   }
    // }
    .mobile-language-select {
      .ant-collapse-content-box {
        span {
          line-height: 19px;
          padding: 10px 0;
        }
      }
    }
  }

  .ant-drawer-body {
    padding: 30px 20px;

    .hot,
    .new {
      .icon {
        margin-left: 6px;
        // transform: translateY(4px);
      }
    }

    > a,
    > div,
    .full-list > li > a {
      display: block;
      line-height: 30px;
      margin-top: 16px;
      font-size: 16px;
      text-indent: 15px;
      border-radius: 5px;
      color: #333;

      &.active {
        color: #fff;
        background-color: #5915e1;
        // font-weight: bold;
        // font-size: 18px;
      }

      > span {
        &.active {
          color: #5915e1;
        }
      }
    }

    .full-list {
      list-style-type: none;
      padding: 0;
    }

    .connect-wallet {
      padding: 5px 0;
      color: #5915e1;
      text-decoration: underline;
      font-size: 14px;
      display: block;
      text-align: center;
      margin: auto;

      &:hover {
        color: #fff;
      }
    }

    .address-text {
      display: flex;
      align-items: center;
      color: #5915e1;
      cursor: pointer;
      padding-bottom: 8px;
      border-radius: 0;
      justify-content: space-between;

      .mobile-logo {
        margin-top: 0;

        &:hover {
          background-color: initial;
        }
      }
    }
  }

  .disabled {
    color: #ccc !important;
    cursor: not-allowed;
  }
}

