@media screen and (max-width: 800px) {
  .banner .banner-tvl .tvl-value {
    font-size: 35px;
    line-height: 40px;
  }

  .banner {
    width: calc(100% - 30px);

    .banner-title {
      margin: 5% 0 0 0%;
      line-height: 36px;
    }

    .banner-des {
      margin: 6px 0 0 0%;
      font-size: 20px;
    }
  }

  #root footer {
    height: auto;
    flex-direction: column;
  }

  #root footer,
  #root footer.pool-footer {
    // background-color: #120222;
    flex-direction: row;
    justify-content: flex-start;
    // bottom: 30px;

    > a {
      margin: 0;
      padding: 0 0.4em;
    }
  }

  #root footer.pool-footer {
    padding-top: 45px;

    // margin-bottom: 50px;
    .footer-box {
      padding-bottom: 30px;
    }
  }

  .token-buttons {
    display: block;

    > button {
      width: 100%;
    }
  }
}

@media screen and (max-width: 750px) {
  // #root .main {
  //   padding-bottom: 60px;
  // }

  .tvl-list {
    padding: 20px 15px 14px;
    margin-top: 16px;
    margin-bottom: 14px;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.1);
  }

  .banner {
    .banner-tvl {
      display: flex;
      flex-direction: column-reverse;
      margin-top: 0;

      .tvl-value {
        margin-top: 4px;
      }
    }

    .banner-tvl-detail {
      display: block;
      margin-bottom: 0;

      > div {
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
        align-items: center;
        margin-bottom: 10px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .tvl-title {
        font-size: 14px;
        line-height: 14px;
        color: #ffffff;
      }

      .tvl-value > span {
        font-weight: bold;
        font-size: 14px;
        line-height: 15px;
        color: #ffffff;
      }
    }
  }
}

