}

.hide-for-mobile {
  @media screen and (min-width: $mobile-width-breakpoint) {
    display: none;
  }
}

.hide-for-pool-tabs-big {
  @media screen and (max-width: 599px) {
    display: none;
  }
}

.hide-for-pool-tabs-small {
  @media screen and (min-width: 600px) {
    display: none;
  }
}

.sun-io-copy-text {
