  height: 40px;
  background: rgba(17, 17, 17, 0.5);
  border-radius: 5px;
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
  line-height: 40px;
  position: fixed;
  left: 50%;
  top: 50%;
